@font-face {
	font-family: "iconfont";
	src: url("../fonts/icomoon.eot");
	src: url("../fonts/icomoon.eot#iefix") format("embedded-opentype"),
	url("../fonts/icomoon.woff") format("woff"),
	url("../fonts/icomoon.ttf") format("truetype"),
	url("../fonts/icomoon.svg") format("svg");
	font-weight: normal;
	font-style: normal;
}

@mixin icomoon($val: '') {
	font-family: 'iconfont';
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	// Better Font Rendering
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	&:before {
		content: $val;
	}
}

[class^="icon"]:before,
[class*=" icon"]:before,
[class^="icon"]:after,
[class*=" icon"]:after,
.icons:before,
.icons:after,
.icon-spinner > span {
	@include icomoon();
}

